	module.exports = {"data":{"contentfulBlogPost":{"title":"Hello world","publishDate":"May 14th, 2017","heroImage":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"//images.ctfassets.net/9ys45gwh7pzh/6Od9v3wzLOysiMum0Wkmme/d56552dc8d58d1d15273a7b4cf8677ac/cameron-kirby-88711.jpg?w=1180&q=50&bg=rgb%3A000000","srcSet":"//images.ctfassets.net/9ys45gwh7pzh/6Od9v3wzLOysiMum0Wkmme/d56552dc8d58d1d15273a7b4cf8677ac/cameron-kirby-88711.jpg?w=295&h=197&q=50&bg=rgb%3A000000 295w,\n//images.ctfassets.net/9ys45gwh7pzh/6Od9v3wzLOysiMum0Wkmme/d56552dc8d58d1d15273a7b4cf8677ac/cameron-kirby-88711.jpg?w=590&h=393&q=50&bg=rgb%3A000000 590w,\n//images.ctfassets.net/9ys45gwh7pzh/6Od9v3wzLOysiMum0Wkmme/d56552dc8d58d1d15273a7b4cf8677ac/cameron-kirby-88711.jpg?w=1180&h=787&q=50&bg=rgb%3A000000 1180w,\n//images.ctfassets.net/9ys45gwh7pzh/6Od9v3wzLOysiMum0Wkmme/d56552dc8d58d1d15273a7b4cf8677ac/cameron-kirby-88711.jpg?w=1770&h=1180&q=50&bg=rgb%3A000000 1770w,\n//images.ctfassets.net/9ys45gwh7pzh/6Od9v3wzLOysiMum0Wkmme/d56552dc8d58d1d15273a7b4cf8677ac/cameron-kirby-88711.jpg?w=2360&h=1573&q=50&bg=rgb%3A000000 2360w,\n//images.ctfassets.net/9ys45gwh7pzh/6Od9v3wzLOysiMum0Wkmme/d56552dc8d58d1d15273a7b4cf8677ac/cameron-kirby-88711.jpg?w=3000&h=2000&q=50&bg=rgb%3A000000 3000w","srcWebp":"//images.ctfassets.net/9ys45gwh7pzh/6Od9v3wzLOysiMum0Wkmme/d56552dc8d58d1d15273a7b4cf8677ac/cameron-kirby-88711.jpg?w=1180&q=50&fm=webp&bg=rgb%3A000000","srcSetWebp":"//images.ctfassets.net/9ys45gwh7pzh/6Od9v3wzLOysiMum0Wkmme/d56552dc8d58d1d15273a7b4cf8677ac/cameron-kirby-88711.jpg?w=295&h=197&q=50&fm=webp&bg=rgb%3A000000 295w,\n//images.ctfassets.net/9ys45gwh7pzh/6Od9v3wzLOysiMum0Wkmme/d56552dc8d58d1d15273a7b4cf8677ac/cameron-kirby-88711.jpg?w=590&h=393&q=50&fm=webp&bg=rgb%3A000000 590w,\n//images.ctfassets.net/9ys45gwh7pzh/6Od9v3wzLOysiMum0Wkmme/d56552dc8d58d1d15273a7b4cf8677ac/cameron-kirby-88711.jpg?w=1180&h=787&q=50&fm=webp&bg=rgb%3A000000 1180w,\n//images.ctfassets.net/9ys45gwh7pzh/6Od9v3wzLOysiMum0Wkmme/d56552dc8d58d1d15273a7b4cf8677ac/cameron-kirby-88711.jpg?w=1770&h=1180&q=50&fm=webp&bg=rgb%3A000000 1770w,\n//images.ctfassets.net/9ys45gwh7pzh/6Od9v3wzLOysiMum0Wkmme/d56552dc8d58d1d15273a7b4cf8677ac/cameron-kirby-88711.jpg?w=2360&h=1573&q=50&fm=webp&bg=rgb%3A000000 2360w,\n//images.ctfassets.net/9ys45gwh7pzh/6Od9v3wzLOysiMum0Wkmme/d56552dc8d58d1d15273a7b4cf8677ac/cameron-kirby-88711.jpg?w=3000&h=2000&q=50&fm=webp&bg=rgb%3A000000 3000w","sizes":"(max-width: 1180px) 100vw, 1180px"}},"body":{"childMarkdownRemark":{"html":"<p>These is your very first content with Contentful, pulled in JSON format using the <a href=\"https://www.contentful.com/developers/docs/references/content-delivery-api/\" title=\"Content Delivery API\">Content Delivery API</a>. Content and presentation are now decoupled, allowing you to focus your efforts in building the perfect app.</p>\n<h2>Your first steps</h2>\n<p>Building with Contentful is easy. First take a moment to get <a href=\"https://www.contentful.com/r/knowledgebase/content-modelling-basics/\" title=\"the basics of content modelling\">the basics of content modelling</a>, which you can set up in the <a href=\"https://app.contentful.com/\" title=\"Contentful Web app\">Contentful Web app</a>. Once you get that, feel free to drop by the <a href=\"https://www.contentful.com/developers/docs/\" title=\"Documentation\">Documentation</a> to learn a bit more about how to build your app with Contentful, in particular the <a href=\"https://www.contentful.com/developers/docs/concepts/apis/\" title=\"API basics\">API basics</a> and each one of our four APIs, as shown below.</p>\n<h3>Content Delivery API</h3>\n<p>The <a href=\"https://www.contentful.com/developers/docs/references/content-delivery-api/\" title=\"Content Delivery API\">Content Delivery API</a> (CDA), available at <code>cdn.contentful.com</code>, is a read-only API for delivering content from Contentful to apps, websites and other media. Content is delivered as JSON data, and images, videos and other media as files.\nThe API is available via a globally distributed content delivery network. The server closest to the user serves all content, both JSON and binary. This minimizes latency, which especially benefits mobile apps. Hosting content in multiple global data centers also greatly improves the availability of content.</p>\n<h3>Content Management API</h3>\n<p>The <a href=\"https://www.contentful.com/developers/docs/references/content-management-api/\" title=\"Content Management API\">Content Management API</a> (CMA), available at <code>api.contentful.com</code>, is a read-write API for managing content. Unlike the Content Delivery API, the management API requires you to authenticate as a Contentful user. You could use the CMA for several use cases, such as:</p>\n<ul>\n<li>Automatic imports from different CMSes like WordPress or Drupal.</li>\n<li>Integration with other backend systems, such as an e-commerce shop.</li>\n<li>Building custom editing experiences. We built the <a href=\"https://app.contentful.com/\" title=\"Contentful Web app\">Contentful Web app</a> on top of this API.</li>\n</ul>\n<h3>Preview API</h3>\n<p>The <a href=\"https://www.contentful.com/developers/docs/concepts/apis/#content-preview-api\" title=\"Content Preview API\">Content Preview API</a>, available at <code>preview.contentful.com</code>, is a variant of the CDA for previewing your content before delivering it to your customers. You use the Content Preview API in combination with a \"preview\" deployment of your website (or a \"preview\" build of your mobile app) that allows content managers and authors to view their work in-context, as if it were published, using a \"preview\" access token as though it were delivered by the CDA.</p>\n<h3>Images API</h3>\n<p>The <a href=\"https://www.contentful.com/developers/docs/concepts/apis/#images-api\" title=\"Images API\">Images API</a>, available at <code>images.contentful.com</code>, allows you to resize and crop images, change their background color and convert them to different formats. Using our API for these transformations lets you upload high-quality assets, deliver exactly what your app needs, and still get all the benefits of our caching CDN.</p>"}}}},"pathContext":{"slug":"hello-world"}}

